.o-select {
  position: relative;
  display: flex;
  align-items: center;
  border: 1px solid #E0E0E0;
  border-radius: 4px;
  background-color: white;
  padding-left: 8px;
  padding-right: 8px;
  min-height: 46px; }
  .o-select > span {
    position: absolute;
    top: 11px;
    left: 50px;
    color: #E0E0E0; }
  .o-select .o-select-img {
    flex-shrink: 0; }
  .o-select .form-control {
    position: relative;
    z-index: 1;
    height: calc(2.0em + 0.75rem);
    border: none;
    background-color: transparent;
    cursor: pointer; }
    .o-select .form-control::placeholder {
      color: #BDBDBD;
      opacity: 1; }
  .o-select::after {
    font-family: 'icomoon';
    content: "\e91c";
    font-size: 1.2em;
    transform-origin: 0 0;
    position: absolute;
    right: 8px;
    top: 10px;
    color: #424242;
    pointer-events: none; }
  .o-select:focus-within {
    border-color: #2196F3; }
  .o-select ul {
    display: none;
    position: absolute;
    top: 48px;
    left: 0px;
    width: 100%;
    background-color: white;
    border: 1px solid #E0E0E0;
    box-shadow: 0px 0px 6px rgba(189, 189, 189, 0.5);
    max-height: 400px;
    overflow-y: auto;
    z-index: 1005; }
    .o-select ul li {
      display: flex;
      align-items: center;
      padding: 8px; }
      .o-select ul li span {
        color: #BDBDBD;
        font-weight: 300; }
      .o-select ul li:hover, .o-select ul li.selected {
        background-color: #FAFAFA;
        cursor: pointer; }
        .o-select ul li:hover *, .o-select ul li.selected * {
          color: #2196F3; }
      .o-select ul li.divider {
        border-top: 1px dashed #ccc;
        margin: 8px 0;
        padding: 0;
        cursor: default; }

